/*THis componenet renders vanilla header with logo and links 

Drop down menu for small screen devices uses this example
https://blog.campvanilla.com/reactjs-dropdown-menus-b6e06ae3a8fe
and is modified
*/

import React from 'react';
import Footer from './Footer'
import Header from './Header'
import HomeImageGallery from './HomeImageGallery'

class Home extends React.Component {
  constructor() {
    super();

    this.state = {
      card_clicked: false,
      card_name: '',
      app_link: '',
      card_count: 0,
      macos_apps: ['CalcVerter','Physics Calculator','Finance Calculator','Engineering Calculator','Health Calculator','Geometry Calculator','Dynamics Calculator','Chemistry Calculator','Fluid Mechanics Calculator','Base Converter Pro','Strong Password Generator','Quick Notes'],
      macos_apps_img_count: [14,4,8,6,15,8,5,3,4,2,3,3],
      macos_apps_id: ['id923932984','id1104781999','id1130694845','id1156440755','id1019029632','id1116628390','id1225872584','id1149303127','id1149304414','id939169935','id1387740382','id1260480179'],
    };
  }

  componentDidMount() {
    this.setState({ card_count: this.state.macos_apps.length})
  }
  
  showCard() {
    this.setState({ card_clicked: true})
  }

  closeCard() {
    this.setState({ card_clicked: false})
  }

  render() {
    var divisions = [];

    if (this.state.card_clicked === false) {
      for (var i = 0; i < this.state.card_count; i++) {
        let chosenCard = this.state.macos_apps[i]
        let chosenCardLink = this.state.macos_apps_id[i]
        divisions.push(
          <div className="cardBox" onClick={() => {this.setState({ card_name: chosenCard }); this.setState({app_link: chosenCardLink}); this.showCard()}}>
            <div className="card">
              <div className="card-front">
                <div className="card-front-div">
                  <img className="card-image-front" src={process.env.PUBLIC_URL + '/ProductImages/macOS/' + chosenCard.split(' ').join('-') + '.png'} alt=""/> 
               </div>
               <p className="card-title-front">{chosenCard}</p>
              </div>
              <div className="card-back">
                <img className="card-image-back" src={process.env.PUBLIC_URL + '/Icons/macOS/' + chosenCard.split(' ').join('-') + '.png'} alt=""/> 
                <p className="card-title-back">{chosenCard}</p>
              </div>
            </div>
          </div>
        )
      }
    } else {
      let index = this.state.macos_apps.indexOf(this.state.card_name)
      let imgCount = this.state.macos_apps_img_count[index];
      var imgArray = [];

      for  (i = 0; i < imgCount; i++) {
        imgArray.push(this.state.card_name.replace(/\s/g, "-") + (i+1));
      }
      
      divisions.push(
        <div className="cardEnlarged">
          <div className="cardEnlargedTop">
          <p className="cardEnlargedTitle">{this.state.card_name}</p>
          <button className="cardEnlargedClose" onClick={() => this.closeCard()}>X</button>
          </div>
          <HomeImageGallery
          imageList = {imgArray}
          OS = {"macOS/"}
          folder = {this.state.card_name.replace(/\s/g, "-") + "/"}
          />
          <div className="App-Links-Home">
            <a className="App-Link-Home1" href={"https://loaysetrak.github.io/#macos/" + this.state.card_name.replace(/\s/g, "-").toLowerCase()}>More Info</a>
            <a className="App-Link-Home2" href={"https://apps.apple.com/app/" + this.state.app_link}>Download Now</a>
          </div>
        </div>
      )
    }
    
    return (
      <div className="Home-Page">
        <Header/>
        <div className="Profile-Division">
          <div className="Profile-Details">
            <p className="Profile-Heading">LOAY SETRAK</p>
            <p className="Profile-Subheading">Full Stack Developer</p>
            <br></br>
            <div className="Profile-Links-Division">
            <img className="Profile-Link-Image" src={process.env.PUBLIC_URL + '/SocialMedia/linkedin.png'} alt=""/> 
            <a className="App-Link" href="https://au.linkedin.com/in/loay-setrak-ab0049109">https://au.linkedin.com/in/loay-setrak-ab0049109</a>
            </div>
            <div className="Profile-Links-Division">
            <img className="Profile-Link-Image" src={process.env.PUBLIC_URL + '/SocialMedia/twitter.png'} alt=""/> 
            <a className="App-Link" href="https://twitter.com/loay_setrak">https://twitter.com/loay_setrak</a>
            </div>
            <div className="Profile-Links-Division">
            <img className="Profile-Link-Image" src={process.env.PUBLIC_URL + '/SocialMedia/instagram.png'} alt=""/> 
            <a className="App-Link" href="https://www.instagram.com/loay.setrak/">https://www.instagram.com/loay.setrak/</a>
            </div>
            <div className="Profile-Links-Division">
            <img className="Profile-Link-Image" src={process.env.PUBLIC_URL + '/SocialMedia/Outlook.png'} alt=""/> 
            <a className="App-Link" href="mailto:loaysetrak.software@outlook.com.au">loaysetrak.software@outlook.com.au</a>
            </div>
          </div>
          <img className="Profile-Image" src={process.env.PUBLIC_URL + '/Images/profile-image.png'} alt=""/> 
        </div>

        <div className="About-Division">
        <p className="About-Heading">About Me</p>
        <br></br>
        <p className="About-Text">
        I am a University graduate of the Bachelor of Information Systems. I have always had a growing interest in App development and I have developed skills during my studies and personal life for mobile, desktop and web application developments, as well as database and system design and development. I have deployed multiple applications to the Apple App Stores for macOS, iOS and iPadOS.<br></br><br></br>
        I am experienced in the following programming platforms:<br></br>
        - Swift<br></br>
        - Object-C<br></br>
        - Java<br></br>
        - JS<br></br>
        - React<br></br>
        - Xcode<br></br>
        - Android Studio<br></br>
        - SQL<br></br>
        - HTML<br></br>
        - PHP<br></br>
        - CSS<br></br>
        </p>

        </div>
        <hr className="Home-Line"></hr>
        <br></br>
        <div className="Home-Page-Bottom">
          {divisions} 
        </div>
        <br></br>
        <Footer/>
      </div>
    );
  }
}



export default Home
